*{box-sizing:border-box;margin:0;padding:0}html,body{box-sizing:content-box}::-webkit-scrollbar{display:none}html:before{left:0}html:after,html:before{content:"";will-change:top left;backface-visibility:hidden;position:fixed;top:0;height:100%;width:20px;background:#fff;z-index:21000}body:before{position:static}html:after{right:0}body{font-family:"Press Start 2P",cursive;box-sizing:border-box}body:after{bottom:0}body:after,body:before{content:"";will-change:top left;backface-visibility:hidden;position:fixed;left:0;width:100%;height:20px;background:#fff;z-index:30000}*,:after,:before{box-sizing:border-box}.page-wrapper{width:100%;box-sizing:border-box;padding:20px;position:relative;min-height:calc(100vh - 20px)}.page{max-width:1200px;margin-left:auto;margin-right:auto;position:relative;padding-top:100px}.page-header{min-height:100vh;margin-left:auto;margin-right:auto;width:100%}.nav-span{max-width:600px;font-size:clamp(36px,5vw,65px)}.text-with-outline{color:#000;transition:text-shadow .3s ease}.text-with-outline:hover{color:#fff;text-shadow:0 0 10px #000}.contact-input{width:100%;max-width:300px;padding:10px;outline:none;color:#000;background-color:rgba(0,0,0,0)}